.inactive{
    width: 100px;
    position: fixed;
    background-color: #ffffff;
    height: 100vh;
    box-sizing: border-box;
    padding: 30px;
    transition: width 0.5s ease-in;
    z-index: 40;
}

.active{
    width: 200px;
    position: fixed;
    background-color: #ffffff;
    z-index: 40;
    height: 100vh;
    box-sizing: border-box;
    padding: 30px;
    transition: width 0.3s  ease-in;
}
.selectImage{
    width: 100px;
    padding: 0.5em;
    border-radius: 0.8em;
    color:white;
    cursor:pointer;
    font-size: 13px;
    font-weight:  normal;
    background-color: #000000;
    
}

.selectImage:hover{
    background-color: #313131;

}
.nameImage{
    font-weight: bold;
    font-size: 13px;
    margin-left: 2px;
}
.containerInputFoto{
    display:'flex';
    margin-top: -30px

}

.containerTable::-webkit-scrollbar {
    width: 8px;     /* Tamaño del scroll en vertical */
    height: 8px;    /* Tamaño del scroll en horizontal */
}

.containerTable::-webkit-scrollbar-thumb {
    background: #2B2D68;
    border-radius: 4px;
}
.containerTable::-webkit-scrollbar-thumb:hover {
    background: #2B2D68;
    box-shadow: 0 0 2px 1px rgba(9, 70, 238, 0.2);
}

/* Cambiamos el fondo cuando esté en active */
.containerTable::-webkit-scrollbar-thumb:active {
    background-color: #2B2D68;
}